.container {
  width: 100%;
  padding: .5rem 1rem;
  margin: 0 auto;
}

.block        {display: block;}
.flex         {display: -webkit-box;display: -ms-flexbox;display: flex;}
.grid         {display: grid;}
.hidden       {display: none;}
.inline-block {display: inline-block;}

.visible   {visibility: visible;}
.invisible {visibility: hidden;}

.flex-col         {-webkit-box-orient: vertical;-webkit-box-direction: normal;-ms-flex-direction: column;flex-direction: column;}
.flex-col-reverse {-webkit-box-orient: vertical;-webkit-box-direction: reverse;-ms-flex-direction: column-reverse;flex-direction: column-reverse;}
.flex-row         {-webkit-box-orient: horizontal;-webkit-box-direction: normal;-ms-flex-direction: row;flex-direction: row;}
.flex-row-reverse {-webkit-box-orient: horizontal;-webkit-box-direction: reverse;-ms-flex-direction: row-reverse;flex-direction: row-reverse;}

.flex-wrap         {-ms-flex-wrap: wrap;flex-wrap: wrap;}
.flex-wrap-reverse {-ms-flex-wrap: wrap-reverse;flex-wrap: wrap-reverse;}
.flex-nowrap       {-ms-flex-wrap: nowrap;flex-wrap: nowrap;}

.flex-1       {-webkit-box-flex: 1;-ms-flex: 1 1 0%;flex: 1 1 0%;}
.flex-auto    {-webkit-box-flex: 1;-ms-flex: 1 1 auto;flex: 1 1 auto;}
.flex-initial {-webkit-box-flex: 0;-ms-flex: 0 1 auto;flex: 0 1 auto;}
.flex-none    {-webkit-box-flex: 0;-ms-flex: none;flex: none;}

.flex-grow-0 {-webkit-box-flex: 0;-ms-flex-positive: 0;flex-grow: 0;}
.flex-grow-1 {-webkit-box-flex: 1;-ms-flex-positive: 1;flex-grow: 1;}

.flex-shrink-0 {-ms-flex-negative: 0;flex-shrink: 0;}
.flex-shrink-1 {-ms-flex-negative: 1;flex-shrink: 1;}

.justify-start   {-webkit-box-pack: start;-ms-flex-pack: start;justify-content: flex-start;}
.justify-end     {-webkit-box-pack: end;-ms-flex-pack: end;justify-content: flex-end;}
.justify-center  {-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center;}
.justify-between {-webkit-box-pack: justify;-ms-flex-pack: justify;justify-content: space-between;}
.justify-around  {-ms-flex-pack: distribute;justify-content: space-around;}
.justify-evenly  {-webkit-box-pack: space-evenly;-ms-flex-pack: space-evenly;justify-content: space-evenly;}

.justify-items-start   {justify-items: start;}
.justify-items-end     {justify-items: end;}
.justify-items-center  {justify-items: center;}
.justify-items-stretch {justify-items: stretch;}

.justify-self-auto    {justify-self: auto;}
.justify-self-start   {justify-self: start;}
.justify-self-end     {justify-self: end;}
.justify-self-center  {justify-self: center;}
.justify-self-stretch {justify-self: stretch;}

.content-center  {-ms-flex-line-pack: center;align-content: center;}
.content-start   {-ms-flex-line-pack: start;align-content: flex-start;}
.content-end     {-ms-flex-line-pack: end;align-content: flex-end;}
.content-between {-ms-flex-line-pack: justify;align-content: space-between;}
.content-around  {-ms-flex-line-pack: distribute;align-content: space-around;}
.content-evenly  {-ms-flex-line-pack: space-evenly;align-content: space-evenly;}

.items-start    {-webkit-box-align: start;-ms-flex-align: start;align-items: flex-start;}
.items-end      {-webkit-box-align: end;-ms-flex-align: end;align-items: flex-end;}
.items-center   {-webkit-box-align: center;-ms-flex-align: center;align-items: center;}
.items-baseline {-webkit-box-align: baseline;-ms-flex-align: baseline;align-items: baseline;}
.items-stretch  {-webkit-box-align: stretch;-ms-flex-align: stretch;align-items: stretch;}

.self-auto    {-ms-flex-item-align: auto;align-self: auto;}
.self-start   {-ms-flex-item-align: start;align-self: flex-start;}
.self-end     {-ms-flex-item-align: end;align-self: flex-end;}
.self-center  {-ms-flex-item-align: center;align-self: center;}
.self-stretch {-ms-flex-item-align: stretch;align-self: stretch;}

.order-1     {-webkit-box-ordinal-group: 2;-ms-flex-order: 1;order: 1;}
.order-2     {-webkit-box-ordinal-group: 3;-ms-flex-order: 2;order: 2;}
.order-3     {-webkit-box-ordinal-group: 4;-ms-flex-order: 3;order: 3;}
.order-4     {-webkit-box-ordinal-group: 5;-ms-flex-order: 4;order: 4;}
.order-5     {-webkit-box-ordinal-group: 6;-ms-flex-order: 5;order: 5;}
.order-6     {-webkit-box-ordinal-group: 7;-ms-flex-order: 6;order: 6;}
.order-first {-webkit-box-ordinal-group: -9998;-ms-flex-order: -9999;order: -9999;}
.order-last  {-webkit-box-ordinal-group: 10000;-ms-flex-order: 9999;order: 9999;}
.order-none  {-webkit-box-ordinal-group: 1;-ms-flex-order: 0;order: 0;}

.grid-cols-1    {grid-template-columns: repeat(1, minmax(0, 1fr));}
.grid-cols-2    {grid-template-columns: repeat(2, minmax(0, 1fr));}
.grid-cols-3    {grid-template-columns: repeat(3, minmax(0, 1fr));}
.grid-cols-4    {grid-template-columns: repeat(4, minmax(0, 1fr));}
.grid-cols-5    {grid-template-columns: repeat(5, minmax(0, 1fr));}
.grid-cols-6    {grid-template-columns: repeat(6, minmax(0, 1fr));}
.grid-cols-7    {grid-template-columns: repeat(7, minmax(0, 1fr));}
.grid-cols-8    {grid-template-columns: repeat(8, minmax(0, 1fr));}
.grid-cols-9    {grid-template-columns: repeat(9, minmax(0, 1fr));}
.grid-cols-10   {grid-template-columns: repeat(10, minmax(0, 1fr));}
.grid-cols-11   {grid-template-columns: repeat(11, minmax(0, 1fr));}
.grid-cols-12   {grid-template-columns: repeat(12, minmax(0, 1fr));}
.grid-cols-none {grid-template-columns: none;}

.col-auto   {grid-column: auto;}
.col-span-1 {grid-column: span 1 / span 1;}
.col-span-2 {grid-column: span 2 / span 2;}
.col-span-3 {grid-column: span 3 / span 3;}
.col-span-4 {grid-column: span 4 / span 4;}
